// npm install styled-componenets
import styled from 'styled-components'
import { Link as LinkR } from 'react-router-dom'
import { Link as LinkS } from 'react-scroll'

export const Nav = styled.nav`
    background: ${({scrollNav}) => (scrollNav ? '#000' : '#010606')};
    height: 80px;
    margin-top:-80px;
    display: flex;
    justify-content:center;
    align-items:center;
    font-size:1rem;
    position: sticky;
    top: 0;
    z-index: 10;
    
    
    @media screen and (max-width: 960px) {
        transition: 0.8s all ease;
    }
    `;
export const NavbarContainer = styled.div`
    display: flex;
    justify-content:space-between;
    height: 80px;
    width: 100%;
    z-index:1;
    padding:0 24px;
    max-width:1100px;
    `;

export const NavLogo = styled(LinkR)`
   color: #773db4 ;
   justify-self: flex-start;
   cursor: pointer;
   font-size: 1.5rem;
   display: flex;
   align-items: center;
   margin-left: 0;
   font-weight: bold;
   text-decoration: none;;

   ${'' /* @media screen and (max-width: 768px) {
       display: flex;
       flex-direction: row;
    justify-content: space-between;
    } */}
   `;

export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width: 768px) {
        color: #fff;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }`;

export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -22px;

    @media screen and (max-width: 768px) {
        display: none;
    }
    `;

export const NavItem = styled.li`
    height:80px;
    `;

export const NavLinks = styled(LinkS, LinkR)`
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    
    &.active {
        border-bottom: 5px solid #773db4;
    }`;

