export const NavLinkIcon = styled.a`
    text-decoration: none;
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    
    &.active {
        border-bottom: 5px solid #773db4;
    }
`
export const Avatar = styled.img`
    vertical-align: middle;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    ${'' /* display: flex; */}
    margin-top:5px;
    margin-right:10px;
    
    ${'' /* margin-left: 24px;
   justify-self: flex-start;
   cursor: pointer;
   display: flex;
   align-items: left; */}
    `